// import { fillRect } from "gdxjs";

const createRenderArrowButtonSystem = () => {
  let buttonTex = [];
  let buttonRect = [];
  let finishRect = {};
  let finishTex;
  let starEffect, SE_WIDHT, SE_HEIGHT;
  let scaleButton = 1;
  let scaleFinish = 0.4;
  return {
    init(worldState, { width, height, assets }) {
      const atlas = assets.auditionAtlas;
      const finishAtlas = assets.finishAtlas;
      const upButton = atlas.findRegion("up", -1);
      const downButton = atlas.findRegion("down", -1);
      const leftButton = atlas.findRegion("left", -1);
      const rightButton = atlas.findRegion("right", -1);
      const upButtonPress = atlas.findRegion("up-press", -1);
      const downButtonPress = atlas.findRegion("down-press", -1);
      const leftButtonPress = atlas.findRegion("left-press", -1);
      const rightButtonPress = atlas.findRegion("right-press", -1);
      const finishButton = atlas.findRegion("finish_press", -1);

      starEffect = finishAtlas.findRegion("finish_start_effect", -1);

      SE_WIDHT = width / 8;
      SE_HEIGHT = (SE_WIDHT * starEffect.height) / starEffect.width;

      const FINISH_WIDTH = width / 4;
      const FINISH_HEIGHT =
        (FINISH_WIDTH * finishButton.height) / finishButton.width;

      const BUTTON_WIDTH = width / 5;
      const BUTTON_HEIGHT = (BUTTON_WIDTH * upButton.height) / upButton.width;

      const horizonalMargin = BUTTON_WIDTH * 0.8;
      const BUTTON_START_LEFT_X =
        (width - (BUTTON_WIDTH * 2 + horizonalMargin)) / 2;
      const BUTTON_START_LEFT_y = (height * 3.95) / 5;

      finishTex = finishButton;
      finishRect = {
        x:
          BUTTON_START_LEFT_X +
          BUTTON_WIDTH +
          horizonalMargin / 2 -
          FINISH_WIDTH / 2,
        y: BUTTON_START_LEFT_y + BUTTON_HEIGHT / 2 - FINISH_HEIGHT / 2,
        width: FINISH_WIDTH,
        height: FINISH_HEIGHT,
      };

      buttonTex = [
        { unpress: leftButton, press: leftButtonPress },
        { unpress: upButton, press: upButtonPress },
        { unpress: rightButton, press: rightButtonPress },
        { unpress: downButton, press: downButtonPress },
      ];
      buttonRect = [
        {
          x: BUTTON_START_LEFT_X,
          y: BUTTON_START_LEFT_y,
          width: BUTTON_WIDTH,
          height: BUTTON_HEIGHT,
        },
        {
          x: BUTTON_START_LEFT_X + BUTTON_WIDTH / 2 + horizonalMargin / 2,
          y: BUTTON_START_LEFT_y - BUTTON_HEIGHT * 0.8,
          width: BUTTON_WIDTH,
          height: BUTTON_HEIGHT,
        },
        {
          x: BUTTON_START_LEFT_X + BUTTON_WIDTH + horizonalMargin,
          y: BUTTON_START_LEFT_y,
          width: BUTTON_WIDTH,
          height: BUTTON_HEIGHT,
        },
        {
          x: BUTTON_START_LEFT_X + BUTTON_WIDTH / 2 + horizonalMargin / 2,
          y: BUTTON_START_LEFT_y + BUTTON_HEIGHT * 0.8,
          width: BUTTON_WIDTH,
          height: BUTTON_HEIGHT,
        },
      ];

      worldState.buttonRect = buttonRect;
      worldState.finishRect = finishRect;
    },
    update(delta, worldState, { batch, camera, width, height, whiteTex }) {
      batch.setProjection(camera.combined);
      batch.begin();
      batch.setColor(1, 1, 1, 1);

      // console.log(worldState.newPhase);
      if (worldState.finishedPhase && !worldState.newPhase) {
        scaleButton = Math.max(0.7, (scaleButton -= delta * 2));
        scaleFinish = Math.min(1, (scaleFinish += delta * 4));
      } else if (!worldState.finishedPhase || worldState.newPhase) {
        scaleButton = Math.min(1, (scaleButton += delta * 2));
        scaleFinish = Math.max(0.4, (scaleFinish -= delta * 4));
      }

      if (worldState.finishedPhase) {
        // start effect
        worldState.starEffect.scale1 += (delta * 47) / 3;
        batch.setColor(1, 1, 1, worldState.starEffect.opacity1);
        starEffect.draw(
          batch,
          finishRect.x + finishRect.width / 2 - SE_WIDHT / 2,
          finishRect.y + finishRect.height / 2 - SE_HEIGHT / 2,
          SE_WIDHT,
          SE_HEIGHT,
          SE_WIDHT / 2,
          SE_HEIGHT / 2,
          0,
          worldState.starEffect.scale1,
          worldState.starEffect.scale1
        );
        batch.setColor(1, 1, 1, 1);
        if (worldState.starEffect.scale1 >= 4) {
          worldState.starEffect.scale2 += (delta * 47) / 3;
          batch.setColor(1, 1, 1, worldState.starEffect.opacity2);
          starEffect.draw(
            batch,
            finishRect.x + finishRect.width / 2 - SE_WIDHT / 2,
            finishRect.y + finishRect.height / 2 - SE_HEIGHT / 2,
            SE_WIDHT,
            SE_HEIGHT,
            SE_WIDHT / 2,
            SE_HEIGHT / 2,
            0,
            worldState.starEffect.scale2,
            worldState.starEffect.scale2
          );
          batch.setColor(1, 1, 1, 1);
        }
        worldState.starEffect.scale1 = Math.min(
          6,
          worldState.starEffect.scale1
        );
        if (worldState.starEffect.scale1 === 6) {
          worldState.starEffect.opacity1 = Math.max(
            0,
            (worldState.starEffect.opacity1 -= delta * 5)
          );
        }

        worldState.starEffect.scale2 = Math.min(
          6,
          worldState.starEffect.scale2
        );
        if (worldState.starEffect.scale2 === 6) {
          worldState.starEffect.opacity2 = Math.max(
            0,
            (worldState.starEffect.opacity2 -= delta * 5)
          );
        }
        // if (worldState.starEffect.opacity2 === 0) {
        //   // worldState.hitFinishButton = false;
        //   // worldState.newPhase = true;
        // }
      }

      for (let i = 0; i < buttonRect.length; i++) {
        let texture;
        if (worldState.keyActivated[i]) {
          texture = buttonTex[i].press;
        } else {
          texture = buttonTex[i].unpress;
        }
        texture.draw(
          batch,
          buttonRect[i].x,
          buttonRect[i].y,
          buttonRect[i].width,
          buttonRect[i].height,
          buttonRect[i].width / 2,
          buttonRect[i].height / 2,
          0,
          scaleButton,
          scaleButton
        );

        // fillRect(
        //   batch,
        //   whiteTex,
        //   buttonRect[i].x,
        //   buttonRect[i].y,
        //   buttonRect[i].width,
        //   buttonRect[i].height
        // );
      }

      finishTex.draw(
        batch,
        finishRect.x,
        finishRect.y,
        finishRect.width,
        finishRect.height,
        finishRect.width / 2,
        finishRect.height / 2,
        0,
        scaleFinish,
        scaleFinish
      );

      batch.end();
    },
  };
};

export default createRenderArrowButtonSystem;
