import { Vector2 } from "gdxjs";
const activateKey = (key, keyActivated) => (keyActivated[key] = true);
const deactivateKey = (key, keyActivated) => (keyActivated[key] = false);

const getRectContainPoint = (x, y, buttonRect) => {
  let index = -1;
  for (let i = 0; i < 4; i++) {
    const rect = buttonRect[i];
    if (
      x > rect.x &&
      x < rect.x + rect.width &&
      y > rect.y &&
      y < rect.y + rect.height
    ) {
      return i;
    }
  }
  return index;
};

const getRectPassButtonPoint = (x, y, rect) => {
  if (
    x > rect.x &&
    x < rect.x + rect.width &&
    y > rect.y &&
    y < rect.y + rect.height
  ) {
    return 1;
  }
  return 0;
};

const checkHitRight = (type, notes, phaseIndex, worldState) => {
  if (type === notes[phaseIndex][worldState.currentCheckingNoteIndex].type) {
    notes[phaseIndex][worldState.currentCheckingNoteIndex].activated = true;
    worldState.zoomOutNoteIndexs.push(worldState.currentCheckingNoteIndex);
    worldState.currentCheckingNoteIndex += 1;
    if (worldState.currentCheckingNoteIndex === notes[phaseIndex].length) {
      worldState.currentCheckingNoteIndex = notes[phaseIndex].length - 1;
      worldState.finishedPhase = true;
    }
  } else {
    // MISS++;
    worldState.currentCheckingNoteIndex = 0;
    for (let i = 0; i < notes[phaseIndex].length; i++) {
      notes[phaseIndex][i].activated = false;
    }
  }
};

const createInputHandleGameScreenSystem = () => {
  const rectTouches = {};
  let coord = new Vector2();

  return {
    init(worldState, { canvas, camera, pixelRatio, inputHandler }) {
      const { buttonRect, notes, finishRect } = worldState;

      // const screenCoord = new Vector2();
      // const worldCoord = new Vector2();
      inputHandler.addEventListener("touchStart", (x, y, id) => {
        // x = x * pixelRatio;
        // y = y * pixelRatio;
        // screenCoord.set(x, y);
        // camera.unprojectVector2(worldCoord, screenCoord);
        coord.setVector(inputHandler.getTouchedWorldCoord());
        // const rectIndex = getRectContainPoint(
        //   worldCoord.x,
        //   worldCoord.y,
        //   buttonRect
        // );
        const rectIndex = getRectContainPoint(coord.x, coord.y, buttonRect);
        if (rectIndex > -1 && !worldState.finishedPhase) {
          rectTouches[id] = rectIndex;
          activateKey(rectIndex, worldState.keyActivated);
          checkHitRight(rectIndex, notes, worldState.phaseIndex, worldState);
        } else if (worldState.finishedPhase) {
          const rectButton = getRectPassButtonPoint(
            coord.x,
            coord.y,
            finishRect
          );
          if (rectButton === 1) {
            if (worldState.canPassPhase) {
              // console.log("hit");
              worldState.hit++;
              // HIT++;
            } else {
              // console.log("miss");
              worldState.miss++;
              worldState.anim[worldState.animIndex] = worldState.missAnim;
              // MISS++;
            }
            // worldState.newPhase = true;
            worldState.hitFinishButton = true;
          }
        }
      });

      inputHandler.addEventListener("touchEnd", (id) => {
        deactivateKey(rectTouches[id], worldState.keyActivated);
      });

      document.addEventListener("keydown", (e) => {
        if (e.which === 65) {
          if (!worldState.finishedPhase) {
            activateKey(0, worldState.keyActivated);
            checkHitRight(0, notes, worldState.phaseIndex, worldState);
          }
        } else if (e.which === 87) {
          if (!worldState.finishedPhase) {
            activateKey(1, worldState.keyActivated);
            checkHitRight(1, notes, worldState.phaseIndex, worldState);
          }
        } else if (e.which === 68) {
          if (!worldState.finishedPhase) {
            activateKey(2, worldState.keyActivated);
            checkHitRight(2, notes, worldState.phaseIndex, worldState);
          }
        } else if (e.which === 83) {
          if (!worldState.finishedPhase) {
            activateKey(3, worldState.keyActivated);
            checkHitRight(3, notes, worldState.phaseIndex, worldState);
          }
        }

        if (worldState.finishedPhase && e.which === 32) {
          if (worldState.canPassPhase) {
            // console.log("hit");
            worldState.hit++;
            // HIT++;
          } else {
            // console.log("miss");
            worldState.miss++;
            worldState.anim[worldState.animIndex] = worldState.missAnim;
            // MISS++;
          }
          worldState.hitFinishButton = true;
        }
      });

      document.addEventListener("keyup", (e) => {
        if (e.which === 65) {
          deactivateKey(0, worldState.keyActivated);
        } else if (e.which === 87) {
          deactivateKey(1, worldState.keyActivated);
        } else if (e.which === 68) {
          deactivateKey(2, worldState.keyActivated);
        } else if (e.which === 83) {
          deactivateKey(3, worldState.keyActivated);
        }
      });
    },
    update() {},
  };
};

export default createInputHandleGameScreenSystem;
