const createBackGroundRenderSystem = () => {
  let tableInfos = {};
  let backGroundInfos = {};
  return {
    init(worldState, { width, height, assets }) {
      let atlas = assets.auditionAtlas;
      const table = atlas.findRegion("table", -1);
      const TABLE_WIDTH = width;
      const TABLE_HEIGHT = (TABLE_WIDTH * table.height) / table.width;
      tableInfos = { tex: table, w: TABLE_WIDTH, h: TABLE_HEIGHT };

      const bg = atlas.findRegion("bg", -1);
      const BG_WIDTH = width;
      const BG_HEIGHT = (BG_WIDTH * bg.height) / bg.width;
      backGroundInfos = { tex: bg, w: BG_WIDTH, h: BG_HEIGHT };
    },
    update(delta, { boundaryY }, { batch, width, height, camera }) {
      batch.setProjection(camera.combined);
      batch.begin();
      batch.setColor(1, 1, 1, 1);
      backGroundInfos.tex.draw(
        batch,
        width / 2 - backGroundInfos.w / 2,
        -backGroundInfos.h / 30,
        backGroundInfos.w,
        backGroundInfos.h
      );

      tableInfos.tex.draw(
        batch,
        width / 2 - tableInfos.w / 2,
        boundaryY,
        tableInfos.w,
        tableInfos.h
      );

      batch.end();
    },
  };
};

export default createBackGroundRenderSystem;
