import { fillRect, createViewportAwareInputHandler } from "gdxjs";
import createRenderGameScreenSystem from "./GameScreen";
import { checkPointInsideRect } from "../utils/coordUtils";

export default (Game) => {
  const {
    batch,
    canvas,
    width,
    height,
    whiteTex,
    camera,
    viewport,
    assets,
  } = Game;

  // const inputHandler = new InputHandler(canvas);
  const inputHandler = createViewportAwareInputHandler(canvas, viewport);
  const atlas = assets.auditionAtlas;
  const startEndAtlas = assets.startEndAtlas;

  const table = atlas.findRegion("table", -1);
  const TABLE_WIDTH = width;
  const TABLE_HEIGHT = (TABLE_WIDTH * table.height) / table.width;

  const bg = atlas.findRegion("bg", -1);
  const BG_WIDTH = width;
  const BG_HEIGHT = (BG_WIDTH * bg.height) / bg.width;

  const curtain = assets.curtain;
  const CURTAIN_WIDTH = width;
  const CURTAIN_HEIGHT = (CURTAIN_WIDTH * curtain.height) / curtain.width;

  const number1 = startEndAtlas.findRegion("number1_start", -1);
  const number2 = startEndAtlas.findRegion("number2_Start", -1);
  const number3 = startEndAtlas.findRegion("number3_start", -1);
  const startButton = startEndAtlas.findRegion("start_button");

  const BUTTON_WIDTH = width / 2;
  const BUTTON_HEIGHT = (BUTTON_WIDTH * startButton.height) / startButton.width;
  const BUTTON_START_X = width / 2 - BUTTON_WIDTH / 2;
  const BUTTON_START_Y = (height * 2.3) / 3;

  const buttonRect = [
    BUTTON_START_X,
    BUTTON_START_Y,
    BUTTON_WIDTH,
    BUTTON_HEIGHT,
  ];

  const NB1_HEIGHT = height / 8;
  const NB1_WIDHT = (NB1_HEIGHT * number1.width) / number1.height;

  const NB2_HEIGHT = height / 8;
  const NB2_WIDHT = (NB2_HEIGHT * number2.width) / number2.height;

  const NB3_HEIGHT = height / 8;
  const NB3_WIDHT = (NB1_HEIGHT * number3.width) / number3.height;

  let startCountDown = false;
  let stateTime = 0;

  const goToGameScreen = async () => {
    Game.setCurrentScreen(await createRenderGameScreenSystem(Game));
  };

  inputHandler.addEventListener("touchStart", (x, y) => {
    let point = inputHandler.getTouchedWorldCoord();
    if (checkPointInsideRect({ x: point.x, y: point.y }, buttonRect)) {
      startCountDown = true;
    }
    // goToGameScreen();
  });

  const update = (delta) => {
    camera.setPosition(0, 0);
    batch.setProjection(camera.combined);
    batch.begin();
    batch.setColor(1, 1, 1, 1);
    bg.draw(
      batch,
      width / 2 - BG_WIDTH / 2,
      -BG_HEIGHT / 30,
      BG_WIDTH,
      BG_HEIGHT
    );

    table.draw(
      batch,
      width / 2 - TABLE_WIDTH / 2,
      height / 2 - height / 20,
      TABLE_WIDTH,
      TABLE_HEIGHT
    );

    fillRect(batch, curtain, 0, 0, CURTAIN_WIDTH, CURTAIN_HEIGHT);

    startButton.draw(
      batch,
      buttonRect[0],
      buttonRect[1],
      buttonRect[2],
      buttonRect[3]
    );

    if (startCountDown && stateTime < 3) {
      let tex;
      let infos;
      if (stateTime < 1) {
        tex = number3;
        infos = [NB3_WIDHT, NB3_HEIGHT];
      } else if (stateTime > 1 && stateTime < 2) {
        tex = number2;
        infos = [NB2_WIDHT, NB2_HEIGHT];
      } else if (stateTime > 2 && stateTime < 3) {
        tex = number1;
        infos = [NB1_WIDHT, NB1_HEIGHT];
      }
      stateTime += delta;
      tex.draw(
        batch,
        width / 2 - infos[0] / 2,
        (height * 1) / 5,
        infos[0],
        infos[1]
      );
    }

    if (stateTime >= 3) {
      goToGameScreen();
    }
    batch.end();
  };

  return {
    update,
    destroy() {
      inputHandler.cleanup();
    },
  };
};
