import loadSCML from "./components/loadSCML";
import loadJsonAtlas from "./components/loadJsonAtlas";
import nudged from "nudged";
import { mat4, vec3 } from "gl-matrix";

function concatAndResolveUrl(url, concat) {
  let url1 = url.split("/");
  let url2 = concat.split("/");
  let url3 = [];

  for (let i = 0, l = url1.length; i < l; i++) {
    if (url1[i] === "..") {
      url3.pop();
    } else {
      url3.push(url1[i]);
    }
  }

  for (let i = 0, l = url2.length; i < l; i++) {
    if (url2[i] === "..") {
      url3.pop();
    } else if (url2[i] === ".") {
      continue;
    } else {
      url3.push(url2[i]);
    }
  }

  return url3.join("/");
}

export default async (gl, scmlUrl, rectName) => {
  const { regionNames, pose, data, rawData } = await loadSCML(scmlUrl);
  const atlasInfos = rawData.atlas.i;

  const transformMatrix = mat4.create();
  const vertices = new Float32Array(20);
  const arr1 = [];
  const arr2 = [];
  const arr3 = [];
  const arr4 = [];
  let xy1 = [];
  let xy2 = [];
  let xy3 = [];
  let xy4 = [];

  const atlas = await loadJsonAtlas(
    gl,
    atlasInfos.map((info) => concatAndResolveUrl(scmlUrl, `../${info.name}`))
  );

  let rectObject;

  const setArr = (arr, v1, v2, v3, v4, v5) => {
    arr.length = 0;
    arr.push(v1, v2, v3, v4, v5);
  };

  const generateVertices = (
    region,
    position,
    rotation,
    scale,
    affine,
    color
  ) => {
    if (region.rotated) {
      setArr(
        arr1,
        position.x - region.height / 2,
        position.y - region.width / 2,
        color,
        region.u,
        region.v
      );
      setArr(
        arr2,
        position.x + region.height / 2,
        position.y - region.width / 2,
        color,
        region.u,
        region.v2
      );

      setArr(
        arr3,
        position.x + region.height / 2,
        position.y + region.width / 2,
        color,
        region.u2,
        region.v2
      );
      setArr(
        arr4,
        position.x - region.height / 2,
        position.y + region.width / 2,
        color,
        region.u2,
        region.v
      );
    } else {
      setArr(
        arr1,
        position.x - region.width / 2,
        position.y - region.height / 2,
        color,
        region.u,
        region.v2
      );
      setArr(
        arr2,
        position.x + region.width / 2,
        position.y - region.height / 2,
        color,
        region.u2,
        region.v2
      );
      setArr(
        arr3,
        position.x + region.width / 2,
        position.y + region.height / 2,
        color,
        region.u2,
        region.v
      );
      setArr(
        arr4,
        position.x - region.width / 2,
        position.y + region.height / 2,
        color,
        region.u,
        region.v
      );
    }

    mat4.identity(transformMatrix);
    mat4.translate(transformMatrix, transformMatrix, [
      position.x,
      position.y,
      0,
    ]);
    mat4.rotateZ(transformMatrix, transformMatrix, rotation.rad);
    mat4.scale(transformMatrix, transformMatrix, [scale.x, scale.y, 1]);
    mat4.translate(transformMatrix, transformMatrix, [
      -position.x,
      -position.y,
      0,
    ]);

    setArr(xy1, arr1[0], arr1[1], 0);
    setArr(xy2, arr2[0], arr2[1], 0);
    setArr(xy3, arr3[0], arr3[1], 0);
    setArr(xy4, arr4[0], arr4[1], 0);

    vec3.transformMat4(xy1, xy1, transformMatrix);
    vec3.transformMat4(xy2, xy2, transformMatrix);
    vec3.transformMat4(xy3, xy3, transformMatrix);
    vec3.transformMat4(xy4, xy4, transformMatrix);

    if (affine) {
      xy1 = affine.transform(xy1);
      xy2 = affine.transform(xy2);
      xy3 = affine.transform(xy3);
      xy4 = affine.transform(xy4);
    }

    vertices[0] = xy1[0];
    vertices[1] = xy1[1];
    vertices[2] = arr1[2];
    vertices[3] = arr1[3];
    vertices[4] = arr1[4];
    vertices[5] = xy2[0];
    vertices[6] = xy2[1];
    vertices[7] = arr2[2];
    vertices[8] = arr2[3];
    vertices[9] = arr2[4];
    vertices[10] = xy3[0];
    vertices[11] = xy3[1];
    vertices[12] = arr3[2];
    vertices[13] = arr3[3];
    vertices[14] = arr3[4];
    vertices[15] = xy4[0];
    vertices[16] = xy4[1];
    vertices[17] = arr4[2];
    vertices[18] = arr4[3];
    vertices[19] = arr4[4];
  };

  let rect;
  const domain = [
    [0, 0],
    [0, 0],
    [0, 0],
  ];
  const range = [
    [0, 0],
    [0, 0],
    [0, 0],
  ];

  let prevX, prevY, prevW, prevH, affine;

  return {
    getAnim() {
      return pose.getAnim();
    },
    getInfos() {
      return { regionNames, pose, data, rawData };
    },
    getAspect() {
      if (!rectObject) {
        return 1;
      }
      return rectObject.w / rectObject.h;
    },
    update(delta) {
      pose.update(delta);
      pose.strike();
    },
    setEntity(entityKey) {
      pose.setEntity(entityKey);
      const entity = pose.data.entity_map[entityKey];
      rectObject = entity.obj_info_map[rectName];
      affine = null;
    },
    setAnim(anim) {
      pose.setAnim(anim);
      affine = null;
    },
    draw(batch, x, y, width, height) {
      if (!rect) {
        rect = pose.object_array.find((obj) => obj.name === rectName);

        const px = rect.world_space.position.x;
        const py = rect.world_space.position.y;
        const pw = rectObject.w * rect.world_space.scale.x;
        const ph = rectObject.h * rect.world_space.scale.y;

        setArr(domain[0], px - pw / 2, py - ph / 2);
        setArr(domain[1], px + pw / 2, py - ph / 2);
        setArr(domain[2], px - pw / 2, py + ph / 2);
      }

      if (
        !affine ||
        x !== prevX ||
        y !== prevY ||
        width !== prevW ||
        height !== prevH
      ) {
        setArr(range[0], x - width / 2, y - height / 2);
        setArr(range[1], x + width / 2, y - height / 2);
        setArr(range[2], x - width / 2, y + height / 2);
        prevX = x;
        prevY = y;
        prevW = width;
        prevH = height;
        affine = nudged.estimate("TS", domain, range);
      }

      pose.object_array.forEach((obj) => {
        if (obj.type !== "sprite") return;
        // !this.printed && (console.log(obj) || (this.printed = true));
        // Space.combine(obj.world_space, obj.local_space, obj.world_space);
        const { position, rotation, scale } = obj.world_space;
        if (
          !regionNames[obj.folder_index] ||
          !regionNames[obj.folder_index][obj.file_index]
        )
          return;
        const region = atlas.findRegion(
          regionNames[obj.folder_index][obj.file_index]
        );
        if (!region) return;
        generateVertices(
          region,
          position,
          rotation,
          scale,
          affine,
          batch.color
        );
        batch.drawVertices(region.texture, vertices, 0);
      });
    },
  };
};
