import { mat4 } from "gl-matrix";
import { unproject } from "gdxjs/lib/mathUtils";
export default (width, height, viewportWidth, viewportHeight) => {
  const combined = mat4.create();
  const transform = mat4.create();
  const invOrtho = mat4.create();
  const lookAtMatrix = mat4.create();
  const position = [0, 0];
  const NEAR = 0;
  const FAR = 1;
  const tmp3 = [0, 0, 0];
  const eye = [0, 0, FAR];
  const center = [0, 0, NEAR];
  const up = [0, 1, 0];

  const update = () => {
    mat4.ortho(
      combined,
      -width / 2,
      width / 2,
      -height / 2,
      height / 2,
      NEAR,
      FAR
    );
    const x = position[0];
    const y = position[1];
    eye[0] = center[0] = x;
    eye[1] = center[1] = y;
    mat4.identity(transform);
    mat4.lookAt(lookAtMatrix, eye, center, up);
    mat4.mul(transform, transform, lookAtMatrix);
    mat4.mul(combined, combined, transform);
    mat4.invert(invOrtho, combined);
  };

  update();
  return {
    combined,

    setMatrix(mat) {
      mat4.copy(combined, mat);
      mat4.invert(invOrtho, combined);
    },

    setYUp(yUp) {
      if (yUp) {
        up[1] = -1;
      } else {
        up[1] = 1;
      }

      update();
    },

    setPosition(x, y) {
      position[0] = x;
      position[1] = y;
      update();
    },

    unprojectVector2(out, input) {
      tmp3[0] = input.x;
      tmp3[1] = input.y;
      tmp3[2] = 0;
      unproject(tmp3, tmp3, invOrtho, 0, 0, viewportWidth, viewportHeight);
      out.set(tmp3[0], tmp3[1]);
    },

    resize(newWidth, newHeight) {
      width = newWidth;
      height = newHeight;
      update();
    },
  };
};
