import xml2json from "./xml2json";
import { Data, Pose } from "spriter-lib";

export default async (packFileUrl) => {
  const regionNames = {};
  let spriter_data = null;
  let spriter_json;
  const packFileContent = await fetch(packFileUrl).then((res) => res.text());
  let match = packFileUrl.match(/.scml$/i);
  if (match !== null) {
    let parser = new DOMParser();
    let xml_text = packFileContent.replace(/&quot;/g, '"');
    let xml = parser.parseFromString(xml_text, "text/xml");
    let json_text = xml2json(xml, "\t");
    json_text = json_text.replace(/"@(.*)":/g, '"$1":');
    let json = JSON.parse(json_text);
    spriter_json = json.spriter_data;
  } else {
    spriter_json = JSON.parse(packFileContent);
  }
  spriter_data = new Data().load(spriter_json);

  spriter_data.folder_array.forEach((folder, index) => {
    regionNames[index] = {};
    folder.file_array.forEach((file, fileIndex) => {
      regionNames[folder.id][fileIndex] = file.name;
    });
  });
  const pose = new Pose(spriter_data);

  return { regionNames, pose, data: spriter_data, rawData: spriter_json };
};
