import "./index.css";

import {
  createBatch,
  createGameLoop,
  loadAtlas,
  loadTexture,
  createViewport,
} from "gdxjs";
import createWhiteText from "gl-white-texture";
import createRenderStartScreenSystem from "./screen/StartScreen";
// import loadSpriterAnimation from "./loadSpriterAnimation";
import loadSpriter from "./loadSpriter";
import { Howl } from "howler";

const init = async () => {
  const canvas = document.getElementById("main");

  const pixelRatio = window.devicePixelRatio;
  // const [width, height] = resizeCanvas(canvas, pixelRatio);

  const [width, height] = [47, 100];
  // const [width, height] = [46 * 10, 100 * 10];
  const viewport = createViewport(canvas, width, height);
  const gl = viewport.getContext();
  const camera = viewport.getCamera();

  const batch = createBatch(gl);
  const whiteTex = createWhiteText(gl);

  const sound = new Howl({
    src: ["ghen_covy.mp3"],
  });

  const Game = {
    completePercentage: 0,
    viewport,
    canvas,
    width,
    height,
    gl,
    batch,
    whiteTex,
    camera,
    pixelRatio,
    sound,
    setCurrentScreen(screen) {
      if (Game.currentScreen) {
        Game.currentScreen.destroy();
      }
      Game.currentScreen = screen;
    },
  };

  const spriter = await loadSpriter(
    gl,
    "./spriter_animations/hands.scml",
    "box_000"
  );
  Game.spriter = spriter;

  const auditionAtlas = await loadAtlas(gl, "./assets/audition_asset.atlas");
  const finishAtlas = await loadAtlas(
    gl,
    "./assets/audition-assets_mark.atlas"
  );
  const startEndAtlas = await loadAtlas(gl, "./assets/start_end.atlas");
  const curtain = await loadTexture(gl, "./assets/curtain.png");

  const assets = { auditionAtlas, finishAtlas, startEndAtlas, curtain };
  Game.assets = assets;

  Game.setCurrentScreen(await createRenderStartScreenSystem(Game));

  gl.clearColor(0, 0, 0, 0);
  const update = (delta) => {
    gl.clear(gl.COLOR_BUFFER_BIT);
    if (Game.currentScreen) {
      Game.currentScreen.update(delta);
    }
  };

  createGameLoop(update);
};

init();
