import { fillRect } from "gdxjs";

const createCurtainRenderSystem = () => {
  let curtainInfors = {};
  return {
    init(worldState, { assets, width }) {
      const curtain = assets.curtain;
      const CURTAIN_WIDTH = width;
      const CURTAIN_HEIGHT = (CURTAIN_WIDTH * curtain.height) / curtain.width;
      curtainInfors = { tex: curtain, w: CURTAIN_WIDTH, h: CURTAIN_HEIGHT };
    },
    update(delta, worldState, { camera, batch }) {
      batch.setProjection(camera.combined);
      batch.begin();
      batch.setColor(1, 1, 1, 1);
      fillRect(
        batch,
        curtainInfors.tex,
        0,
        0,
        curtainInfors.w,
        curtainInfors.h
      );
      batch.end();
    },
  };
};

export default createCurtainRenderSystem;
