const createButtonRenderSystem = () => {
  let buttonTex = [];

  return {
    init(worldState, { assets }) {
      const atlas = assets.auditionAtlas;

      const downBtn = atlas.findRegion("down-btn", -1);
      const upBtn = atlas.findRegion("up-btn", -1);
      const leftBtn = atlas.findRegion("left-btn", -1);
      const rightBtn = atlas.findRegion("right-btn", -1);

      const downBtnGray = atlas.findRegion("down-btn-gray", -1);
      const upBtnGray = atlas.findRegion("up-btn-gray", -1);
      const leftBtnGray = atlas.findRegion("left-btn-gray", -1);
      const rightBtnGray = atlas.findRegion("right-btn-gray", -1);

      buttonTex = [
        { active: leftBtn, passive: leftBtnGray },
        { active: upBtn, passive: upBtnGray },
        { active: rightBtn, passive: rightBtnGray },
        { active: downBtn, passive: downBtnGray },
      ];
    },
    update(
      delta,
      {
        notes,
        noteInfos,
        currentCheckingNoteIndex,
        phaseIndex,
        zoomOutNoteIndexs,
        zoomOutNoteInfos,
        newPhase,
      },

      { batch, width, height, camera }
    ) {
      let currentNotesPhase = noteInfos[phaseIndex];
      let currentNotesInfos = notes[phaseIndex];
      let texture;

      for (let i = 0; i < zoomOutNoteIndexs.length; i++) {
        let index = zoomOutNoteIndexs[i];
        zoomOutNoteInfos.push({
          tex: buttonTex[currentNotesInfos[index].type].passive,
          scale: 2,
          postion: currentNotesPhase[index],
        });
        zoomOutNoteIndexs.splice(i, 1);
      }

      batch.setProjection(camera.combined);
      batch.begin();
      batch.setColor(1, 1, 1, 1);
      if (!newPhase) {
        for (let i = 0; i < currentNotesPhase.length; i++) {
          let note = currentNotesPhase[i];
          texture =
            currentNotesInfos[i].activated === true
              ? buttonTex[currentNotesInfos[i].type].active
              : buttonTex[currentNotesInfos[i].type].passive;
          texture.draw(batch, note.x, note.y, note.width, note.height);

          if (zoomOutNoteInfos[i]) {
            zoomOutNoteInfos[i].scale -= delta * 4;
            let { tex, postion } = zoomOutNoteInfos[i];
            batch.setColor(1, 1, 1, 0.5);
            tex.draw(
              batch,
              postion.x,
              postion.y,
              postion.width,
              postion.height,
              postion.width / 2,
              postion.height / 2,
              0,
              zoomOutNoteInfos[i].scale,
              zoomOutNoteInfos[i].scale
            );
            batch.setColor(1, 1, 1, 1);
            if (zoomOutNoteInfos[i].scale <= 1.1) {
              zoomOutNoteInfos.splice(i, 1);
            }
          }
        }
      }
      batch.end();
    },
  };
};

export default createButtonRenderSystem;
