import { loadTexture } from "gdxjs";
import TextureRegion from "./textureRegion";
function concatAndResolveUrl(url, concat) {
  let url1 = url.split("/");
  let url2 = concat.split("/");
  let url3 = [];

  for (let i = 0, l = url1.length; i < l; i++) {
    if (url1[i] === "..") {
      url3.pop();
    } else if (url1[i] === ".") {
      continue;
    } else {
      url3.push(url1[i]);
    }
  }

  for (let i = 0, l = url2.length; i < l; i++) {
    if (url2[i] === "..") {
      url3.pop();
    } else if (url2[i] === ".") {
      continue;
    } else {
      url3.push(url2[i]);
    }
  }
  return url3.join("/");
}
const loadUrl = async (gl, packFileUrl, textureOptions, textureRegions) => {
  const packFileContent = await fetch(packFileUrl).then((res) => res.text());
  const regions = [];
  const sites = [];
  const obj = JSON.parse(packFileContent);
  const meta = obj.meta;
  let name = Object.getOwnPropertyNames(obj.frames);
  const framesArray = Object.keys(obj.frames).map((i) => obj.frames[i]);
  const file = concatAndResolveUrl(packFileUrl, `../${meta.image}`);
  for (let i = 0; i < framesArray.length; i++) {
    const frame = framesArray[i];
    let w = frame.frame.w,
      h = frame.frame.h;
    const site = {
      // const frame: any = tps_json.frames[key],
      // const site: Site = (data.sites[key] = new Site());
      x: frame.frame.x,
      y: frame.frame.y,
      w: frame.frame.w,
      h: frame.frame.h,
      rotate: frame.rotated ? 1 : 0, // 90 degrees
      offset_x: (frame.spriteSourceSize && frame.spriteSourceSize.x) || 0,
      offset_y: (frame.spriteSourceSize && frame.spriteSourceSize.y) || 0,
      original_w: (frame.sourceSize && frame.sourceSize.w) || w,
      original_h: (frame.sourceSize && frame.sourceSize.h) || h,
    };
    sites.push(site);
    const region = {
      id: i,
      name: name[i],
      frame: frame.frame,
      rotated: frame.rotated === "true",
      sourceSize: frame.sourceSize,
      spriteSourceSize: frame.spriteSourceSize,
      trimmed: frame.trimmed,
      app: meta.app,
      format: meta.format,
      main_texture: meta.image,
      scale: meta.scale,
      size: meta.size,
      version: meta.version,
      site: site,
      meta: meta,
    };
    regions.push(region);
  }
  const tex = await loadTexture(gl, file, textureOptions);
  for (const region of regions) {
    const x = region.frame.x;
    const y = region.frame.y;
    const w = region.rotated ? region.frame.h : region.frame.w;
    const h = region.rotated ? region.frame.w : region.frame.h;
    const originX = region.rotated ? region.sourceSize.h : region.sourceSize.w;
    const originY = region.rotated ? region.sourceSize.w : region.sourceSize.h;
    const extraData = region;
    const invTextWidth = 1 / region.size.w;
    const invTextHeight = 1 / region.size.h;
    textureRegions.push(
      new TextureRegion(
        tex,
        x,
        y,
        w,
        h,
        originX,
        originY,
        extraData,
        invTextWidth,
        invTextHeight
      )
    );
  }
  return textureRegions;
};
export default async (gl, listPackFileUrl, textureOptions) => {
  let textureRegions = [];
  if (Array.isArray(listPackFileUrl)) {
    for (const packFileUrl of listPackFileUrl) {
      textureRegions = await loadUrl(
        gl,
        packFileUrl,
        textureOptions,
        textureRegions
      );
    }
  } else {
    textureRegions = await loadUrl(
      gl,
      listPackFileUrl,
      textureOptions,
      textureRegions
    );
  }
  return {
    getRegions() {
      return textureRegions;
    },
    findRegion(name) {
      for (let i = 0; i < textureRegions.length; i++) {
        if (textureRegions[i].name === name) {
          return textureRegions[i];
        }
      }
    },
  };
};
