export default class TextureRegion {
  static splitTexture(texture, cols, rows) {
    const { width, height } = texture;
    const regionWidth = width / cols;
    const regionHeight = height / rows;
    const regions = [];

    for (let x = 0; x < cols; x++) {
      for (let y = 0; y < rows; y++) {
        regions.push(
          new TextureRegion(
            texture,
            x * regionWidth,
            y * regionHeight,
            regionWidth,
            regionHeight,
            {}
          )
        );
      }
    }

    return regions;
  }
  constructor(
    tex,
    x,
    y,
    width,
    height,
    originX,
    originY,
    extraData,
    invTexWidth,
    invTexHeight
  ) {
    if (!invTexWidth || !invTexHeight) {
      invTexWidth = 1 / tex.width;
      invTexHeight = 1 / tex.height;
    }
    const u = x * invTexWidth;
    const v = y * invTexHeight;
    const u2 = (x + width) * invTexWidth;
    const v2 = (y + height) * invTexHeight;
    this.rotated = false;
    this.texture = tex;
    this.x = x;
    this.y = y;
    this.originX = originX;
    this.originY = originY;
    this.width = width;
    this.height = height;
    this.u = u;
    this.v = v;
    this.u2 = u2;
    this.v2 = v2;
    for (let key in extraData) {
      this[key] = extraData[key];
    }
  }

  draw(
    batch,
    x,
    y,
    width,
    height,
    originX = 0,
    originY = 0,
    rotation = 0,
    scaleX = 1,
    scaleY = 1
  ) {
    batch.draw(
      this.texture,
      x,
      y,
      width,
      height,
      this.rotated,
      originX,
      originY,
      rotation,
      scaleX,
      scaleY,
      this.u,
      this.v,
      this.u2,
      this.v2
    );
  }
}
