import { createViewportAwareInputHandler, fillRect } from "gdxjs";
import createRenderStartScreenSystem from "./StartScreen";
import { checkPointInsideRect } from "../utils/coordUtils";

const caculateString = (infos, numberInfos, width, percentageInfos) => {
  let sumLength = 0;
  let numberList = [];
  for (let i = 0; i < infos.length; i++) {
    sumLength += numberInfos[i].width;
  }
  sumLength += percentageInfos.width;
  const START_X = width / 2 - sumLength / 2;

  let distanceTmp = 0;
  for (let i = 0; i < infos.length; i++) {
    let numberWidth = numberInfos[i - 1] ? numberInfos[i - 1].width : 0;
    distanceTmp += numberWidth;
    numberList.push({
      tex: numberInfos[infos[i]].tex,
      x: START_X + distanceTmp,
      width: numberInfos[i].width,
    });
  }
  distanceTmp += numberInfos[numberInfos.length - 1].width;
  numberList.push({
    tex: percentageInfos.tex,
    x: START_X + distanceTmp,
    width: percentageInfos.width,
  });

  return numberList;
};

const getStringInfos = (result) => {
  let infos = [];
  for (let i = 0; i < result.length; i++) {
    const char = result.charAt(i);
    infos.push(parseInt(char));
  }
  return infos;
};

const createRenderEndScreenSystem = (Game) => {
  const goToStartScreen = async () => {
    Game.setCurrentScreen(await createRenderStartScreenSystem(Game));
  };
  const { batch, canvas, width, height, camera, viewport, assets } = Game;
  const inputHandler = createViewportAwareInputHandler(canvas, viewport);
  const atlas = assets.auditionAtlas;
  const startEndAtlas = assets.startEndAtlas;

  const table = atlas.findRegion("table", -1);
  const TABLE_WIDTH = width;
  const TABLE_HEIGHT = (TABLE_WIDTH * table.height) / table.width;

  const bg = atlas.findRegion("bg", -1);
  const BG_WIDTH = width;
  const BG_HEIGHT = (BG_WIDTH * bg.height) / bg.width;

  const curtain = assets.curtain;
  const CURTAIN_WIDTH = width;
  const CURTAIN_HEIGHT = (CURTAIN_WIDTH * curtain.height) / curtain.width;

  const restartButton = startEndAtlas.findRegion("restart_button", -1);
  const quote = startEndAtlas.findRegion("quote", -1);
  const yourScore = startEndAtlas.findRegion("your_score", -1);
  const percentage = startEndAtlas.findRegion("percentage", -1);

  const RB_WIDTH = width / 4.5;
  const RB_HEIGHT = (RB_WIDTH * restartButton.height) / restartButton.width;
  const RB_START_X = width / 2 - RB_WIDTH / 2;
  const RB_START_Y = (height * 2.3) / 3;

  const buttonRect = [RB_START_X, RB_START_Y, RB_WIDTH, RB_HEIGHT];

  const Q_WIDHT = width * 0.95;
  const Q_HEIGHT = (Q_WIDHT * quote.height) / quote.width;

  const YS_WIDTH = width / 1.8;
  const YS_HEIGH = (YS_WIDTH * yourScore.height) / yourScore.width;

  const number0 = startEndAtlas.findRegion("0_end", -1);
  const number1 = startEndAtlas.findRegion("1_end", -1);
  const number2 = startEndAtlas.findRegion("2_end", -1);
  const number3 = startEndAtlas.findRegion("3_end", -1);
  const number4 = startEndAtlas.findRegion("4_end", -1);
  const number5 = startEndAtlas.findRegion("5_end", -1);
  const number6 = startEndAtlas.findRegion("6_end", -1);
  const number7 = startEndAtlas.findRegion("7_end", -1);
  const number8 = startEndAtlas.findRegion("8_end", -1);
  const number9 = startEndAtlas.findRegion("9_end", -1);

  const NB_HEIGHT = height / 8;
  const NB0_WIDHT = (NB_HEIGHT * number0.width) / number0.height;
  const NB1_WIDHT = (NB_HEIGHT * number1.width) / number1.height;
  const NB2_WIDHT = (NB_HEIGHT * number2.width) / number2.height;
  const NB3_WIDHT = (NB_HEIGHT * number3.width) / number3.height;
  const NB4_WIDHT = (NB_HEIGHT * number4.width) / number4.height;
  const NB5_WIDHT = (NB_HEIGHT * number5.width) / number5.height;
  const NB6_WIDHT = (NB_HEIGHT * number6.width) / number6.height;
  const NB7_WIDHT = (NB_HEIGHT * number7.width) / number7.height;
  const NB8_WIDHT = (NB_HEIGHT * number8.width) / number8.height;
  const NB9_WIDHT = (NB_HEIGHT * number9.width) / number9.height;

  const numberInfos = [
    { tex: number0, width: NB0_WIDHT },
    { tex: number1, width: NB1_WIDHT },
    { tex: number2, width: NB2_WIDHT },
    { tex: number3, width: NB3_WIDHT },
    { tex: number4, width: NB4_WIDHT },
    { tex: number5, width: NB5_WIDHT },
    { tex: number6, width: NB6_WIDHT },
    { tex: number7, width: NB7_WIDHT },
    { tex: number8, width: NB8_WIDHT },
    { tex: number9, width: NB9_WIDHT },
  ];

  const PT_HEIGHT = NB_HEIGHT;
  const PT_WIDTH = (PT_HEIGHT * percentage.width) / percentage.height;
  const percentageInfos = { tex: percentage, width: PT_WIDTH };

  const result = Game.completePercentage.toString();
  const stringInfos = getStringInfos(result, numberInfos);
  const numberList = caculateString(
    stringInfos,
    numberInfos,
    width,
    percentageInfos
  );

  inputHandler.addEventListener("touchStart", (x, y) => {
    const point = inputHandler.getTouchedWorldCoord();
    if (checkPointInsideRect({ x: point.x, y: point.y }, buttonRect)) {
      Game.completePercentage = 0;
      goToStartScreen();
    }
  });

  const update = (delta) => {
    // console.log(numberList);
    batch.setProjection(camera.combined);
    batch.begin();
    batch.setColor(1, 1, 1, 1);
    bg.draw(
      batch,
      width / 2 - BG_WIDTH / 2,
      -BG_HEIGHT / 30,
      BG_WIDTH,
      BG_HEIGHT
    );

    table.draw(
      batch,
      width / 2 - TABLE_WIDTH / 2,
      height / 2 - height / 20,
      TABLE_WIDTH,
      TABLE_HEIGHT
    );

    fillRect(batch, curtain, 0, 0, CURTAIN_WIDTH, CURTAIN_HEIGHT);

    restartButton.draw(batch, RB_START_X, RB_START_Y, RB_WIDTH, RB_HEIGHT);
    quote.draw(batch, width / 2 - Q_WIDHT / 2, height / 2, Q_WIDHT, Q_HEIGHT);
    yourScore.draw(
      batch,
      width / 2 - YS_WIDTH / 2,
      height / 4,
      YS_WIDTH,
      YS_HEIGH
    );

    for (let i = 0; i < numberList.length; i++) {
      numberList[i].tex.draw(
        batch,
        numberList[i].x,
        height / 3,
        numberList[i].width,
        NB_HEIGHT
      );
    }

    batch.end();
  };
  return {
    update,
    destroy() {
      inputHandler.cleanup();
    },
  };
};

export default createRenderEndScreenSystem;
