import createOrthoCamera2 from "../components/orthoCamera2";
import { resizeCanvas } from "gdxjs";

const createHandAnimationRenderSystem = () => {
  let camera;
  const animOffset = {
    buom_bay_right: {
      x: 5,
      y: -3,
    },
    xoa_tay: {
      x: -5,
      y: -5,
    },
    buom_bay_left: {
      x: 3,
      y: -3,
    },
    choang_nhau: {
      x: 5,
      y: 0,
    },
    tinh_ban: {
      x: 5,
      y: 2,
    },
    ca_sau: {
      x: 4,
      y: 3,
    },
  };
  let offsetX, offsetY, DRAW_WIDTH, DRAW_HEIGHT;
  return {
    init(worldState, { width, height, canvas, pixelRatio, spriter }) {
      const [cWidth, cHeight] = resizeCanvas(canvas, pixelRatio);
      camera = createOrthoCamera2(width, height, cWidth, cHeight);
      DRAW_WIDTH = width;
      DRAW_HEIGHT = DRAW_HEIGHT = width / spriter.getAspect();
      spriter.setEntity("hands");
      // spriter.setAnim(worldState.anim[worldState.animIndex]);
    },
    update(delta, worldState, { spriter, batch, width, height, pixelRatio }) {
      batch.setProjection(camera.combined);
      batch.begin();
      spriter.setAnim(worldState.anim[worldState.animIndex]);
      spriter.update(delta * 1000);
      offsetX = 0;
      offsetY = 0;
      const offset = animOffset[spriter.getAnim()];
      if (offset) {
        offsetX = offset.x;
        offsetY = offset.y;
      }
      spriter.draw(
        batch,
        0 + offsetX,
        height / 2 - DRAW_HEIGHT / 2 + offsetY,
        DRAW_WIDTH,
        DRAW_HEIGHT,
        width / height
      );
      batch.end();
    },
  };
};

export default createHandAnimationRenderSystem;
